import React, { Component } from 'react';
import { PlayIcon, SettingsIcon, VolumeIcon} from './icon';

class MenuButton extends Component {
		constructor(props) {
    	super(props);
    	this.state = { clicked: false };
  	}

	render() {
		const items = [<PlayIcon name="Instrument"/>, <VolumeIcon name="Volume"/>, <SettingsIcon name="Settings" />];
		const names = "vmenu-item box-shadow";

		const listItems = items.map((item, index) => {
			return(
				<li onClick={index <= 1? this.props.handleClick:this.props.handleSettings} key={index + 1} className={names}>
					<div className={this.props.baseclass + this.props.classname + this.props.name}>
						<div onClick={this.props.handleClick} className="btn gugi">
							{item}
						</div>
					</div>
				</li>
			)
		});

		return(
			<React.Fragment>
			{listItems}
			</React.Fragment>
		)
	}
}

export default MenuButton;


